.about {
    background-color: #121224; /* Keep same */
    text-align: center;
}

.about h2 {
    color: #4DB5FF;
    margin-bottom: 25px;
    position: relative; /* for the underline */
    font-size: 1.5rem;

}

/* Blue line under heading */
.about h2::after {
    content: "";
    width: 80px;
    height: 3px;
    background: #4DB5FF;
    display: block;
    margin: 10px auto 0 auto;
    border-radius: 2px;
}

/* About text */
.about-text {
    max-width: 700px;
    margin: 0 auto 80px auto; /* more space for balance */
    text-align: center;
    color: #ccc;
    font-size: 1rem;
    line-height: 1.6;
}

/* Highlighted Intro Line */
.highlight-text {
    font-size: 1.2rem;
    font-weight: 500;
    line-height: 1.8;
    color: #eee;
    text-align: center;
    margin-bottom: 20px;
    animation: fadeIn 2s ease-in-out;
}

.highlight-text .role,
.highlight-text .cloud,
.highlight-text .ai {
    color: #4DB5FF;
    font-weight: 700;
}

/* Fade in effect */
@keyframes fadeIn {
    from { opacity: 0; transform: translateY(15px); }
    to { opacity: 1; transform: translateY(0); }
}

/* Service Boxes */
.services-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
}

.service-box {
    background-color: #2A2A4A;
    padding: 30px 20px;
    width: 250px;
    border-radius: 12px;
    box-shadow: 0 8px 20px rgba(0,0,0,0.15);
    transition: 0.3s ease;
    text-align: center;
    border: 2px solid #4DB5FF;
}

.service-box:hover {
    transform: translateY(-5px);
    box-shadow: 0 12px 25px rgba(0,0,0,0.25);
}

.service-box h3 {
    margin-bottom: 12px;
    color: #4DB5FF;
    font-size: 1.2rem;
}

.service-box p {
    color: #ccc;
    font-size: 0.95rem;
}
